/* purgecss start ignore */

.lc-multiline-input {
  position: relative;
}

.lc-multiline-input-character-limit {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-gray), var(--tw-text-opacity));
    z-index: 2;
    position: absolute;
    right: 44px;
    bottom: 16px;
    font-size: 10px;
    pointer-events: none;
  }

.lc-multiline-input-resize {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-gray), var(--tw-text-opacity));
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    right: 12px;
    bottom: 12px;
    width: 24px;
    height: 24px;
    background: #fff;
    pointer-events: none;
    border-radius: 7px;
  }

/* purgecss end ignore */
